    <Card.Title style={{fontSize:"60px", color:"#333"}}>CAD<br/><p style={{fontSize:"35px", color:"#555", fontWeight:"500"}}>{cad.toString().substring(0, 6)}</p></Card.Title>
    <Card.Text>
